section {
    padding: 0px 0px;
    margin: 20px 10px;
    background-color: #f4f4f4;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.section-image {
  width: 100%;
  max-height: 500px; /* Maintain the aspect ratio */
  margin: 0 auto;
  display: block;
  border-radius: 8px;
}
section h1 {
    margin: 0 0;
    font-size: larger;
    font-weight: bold;
}
section p{
   font-size: medium;
}
p {
    text-align:justify !important;
  }

